@import url('https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@400;700;800&display=swap');

$흰색:#fff;
$진회색:#777;
$검정색:#000;
$연회색:#9b9b9b;
$너비:980px;

$작은폰트:12px;
$중간폰트:14px;

.height {
    height: 51px;
}

@mixin 라인하이트() {
    line-height: 51px;
}

body,
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
li {
    margin: 0;
    padding: 0;
}

ol,
ul {
    list-style: none;
}

a {
    text-decoration: none;
}

body {
    font-family: 'Nanum Gothic', sans-serif;
}

.main_header {
    width: $너비;
    margin: auto;
    display: flex;
    @extend .height;

    .h1_logo {
        flex: 1;
        padding-top: 3px;
        margin-right: 10px;

        a {
            display: block;
            width: 103px;
            height: 25px;

            img {
                width: 100%;
                vertical-align: middle;
            }
        }
    }

    .navlist1 {
        flex: 7;
        display: flex;
        font-size: $작은폰트;
        @include 라인하이트();

        li {
            letter-spacing: -1.5px;

            &:not(:last-child) {
                padding-right: 12px;
                background: url('./header_line_bg.gif') no-repeat 94% 20px;
            }

            a {
                color: $진회색;
            }
        }
    }

    .navlist2 {
        flex: 2;
        display: flex;
        font-size: $작은폰트;
        @include 라인하이트();

        li {
            padding-left: 14px;
            background: url('./header_middleLine_bg.gif') no-repeat left 24% center;

            a {
                display: block;
                width: 100%;
                color: $진회색;
                padding-left: 6px;
            }
        }
    }
}